@import (css) url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');
@import './_hero.less';

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    color: #fff;
}

.container{
    max-width: 960px;
    width: 100%;
    margin: 0 auto;

    @media (max-width:640px){
        max-width: 80%;
    }

    @media (max-width:1024px){
        max-width: 90%;
    }
}